<template>
  <div id="BookManage">
    <div id="head">
      <ul>
        <li>
          <el-button type="primary" @click="adddata"><i class="el-icon-circle-plus"/></el-button>
        </li>
        <li></li>
        <li>
          <el-select v-model="value" placeholder="">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
        </li>
        <li>
          <span>  </span>
        </li>
        <li @keyup.enter="searchBook">
          <el-input placeholder="" v-model:value="searchValue"></el-input>
        </li>
        <li>
          <el-button type="primary" @click="searchBook"></el-button>
        </li>
      </ul>
    </div>
    <div id="main">
      <el-empty description="" v-show="false"></el-empty>
      <div id="sort">
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
      <div id="detail">
        <ul v-for="(item,index) in libs">
          <li><input :disabled="true" v-model:onfocusout="libs[index].mid"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].name"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].publishDate"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].amount"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].borrowstate"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].classify"></li>
          <li>
            <el-button id="amend" type="primary" v-show="editAble[index]" @click="amending(index)"></el-button>
            <el-button id="save" type="primary" v-show="showSaved[index]" @click="saving(index)"></el-button>
            <el-button id="delete" type="primary" @click="deleting(index)"></el-button>
          </li>
        </ul>
      </div>
    </div>
    <div id="bottom">
      <ul>
        <li>
          <el-button type="primary" @click="homepage"></el-button>
        </li>
        <li>
          <el-button @click="lastpage"></el-button>
        </li>
        <li>{{ nowPage }}/{{ totalPage }}</li>
        <li>
          <el-button @click="nextpage"></el-button>
        </li>
        <li>
          <el-button type="primary" @click="trailerpage"></el-button>
        </li>
        <li>{{ this.libs.length }}</li>
      </ul>
    </div>
  </div>
</template>

<!--vue-->
<script>
import axios from "axios";

export default {
  name: "MagazineManage",
  data() {
    return { //return
      libs: [],
      libsSearch: [],//
      searchValue: "",
      libsDatabase: [],//
      transitDatabase:[],//
      nowPage: 1,
      totalPage: 1,
      showSaved: [false, false, false, false, false, false, false, false],
      editAble: [true, true, true, true, true, true, true, true],
      saveMode: 'amend',
      options: [
        {
          value: 'mid',
          label: ''
        },{
          value: 'name',
          label: ''
        },{
          value: 'publishDate',
          label: ''
        },{
          value: 'amount',
          label: ''
        },{
          value: 'borrowstate',
          label: ''
        },{
          value: 'classify',
          label: ''
        },],
      value: 'name'
    }
  },
  methods: {

    searchBook() {
      if (this.searchValue !== "") {
        this.libsSearch = []; //
        for (let i = 0; i < this.libsDatabase.length; i++) {
          switch (this.value) {
            case "mid":
              if (this.libsDatabase[i].mid === this.searchValue) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "name":  //
              if (this.strMatch(this.libsDatabase[i].name, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "publishDate"://
              if (this.strMatch(this.libsDatabase[i].publishDate, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "amount":
              if (this.libsDatabase[i].amount === parseInt(this.searchValue)) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "borrowstate":
              if (this.libsDatabase[i].borrowstate === this.searchValue) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "classify"://
              if (this.strMatch(this.libsDatabase[i].classify, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
          }
        }
        ;

      }this.showData();
    },

    strMatch(str, key) {// 
      let newkey = key.split(/\s/g);//
      for (let i = 0; i < newkey.length; i++) {
        if (str.match(newkey[i]) != null) {
          return true //matchtrue
        }
      }
      return false
    },

    showData() {
      if (this.searchValue !== "") {
        this.libsDatabase = this.libsSearch;
        this.initalize();
      } else {
        this.linkDatabase();
      }
    },


    adddata() {//
      if (this.libs.length < 8) { //
        if (this.editAble[this.libs.length - 1] !== false) {
          //Mxxxxmid
          let midNum = parseInt(this.libs[this.libs.length - 1].mid.slice(-4)) + 1; //mid0
          let midNumLen = midNum.toString().length;//mid
          let midNew;//mid
          switch (midNumLen) {
            case 1:
              midNew = "M000" + midNum.toString();
              break;
            case 2:
              midNew = "M00" + midNum.toString();
              break;
            case 3:
              midNew = "M0" + midNum.toString();
              break;
            case 4:
              midNew = "M" + midNum.toString();
              break;
          }
          this.libs.push({
            mid: midNew,
            name: "",
            publishDate: "",
            amount: 0,
            borrowstate: "",
            classify: "",
          });
          this.libsDatabase.push(this.libs[this.libs.length - 1]);
          this.saveMode = "insert";
          this.editAble[this.libs.length - 1] = false;
          this.showSaved[this.libs.length - 1] = true;
        }
      } else if (this.libs.length === 8) {//
        if ((this.totalPage - this.nowPage) >= 1) { //
          this.trailerpage();//
          this.adddata();//,
        } else if ((this.totalPage - this.nowPage) === 0) { //
          this.libs = [];//
          this.totalPage++;
          this.nowPage++;
          this.adddata();
        }
      }
    },
    // 
    amending(index) {
      this.editAble[index] = false;
      this.showSaved[index] = true;
      this.saveMode = "amend";
      this.$forceUpdate(); //
    },
    // 
    saving(index) {
      let that=this;
      if (this.libs[index].name === "" || this.libs[index].publishDate === "" || this.libs[index].classify === "") { //
        this.$message({
          message: '',
          type: 'warning'
        });
      } else {
        this.editAble[index] = true;
        this.showSaved[index] = false;
      /*  if (this.saveMode === "amend") {
          axios({    //
            method: 'post',
            url: 'http://localhost:3000/magazineManage/amend',
            params: {
              mid: this.libs[index].mid,
              name: this.libs[index].name,
              publishDate: this.libs[index].publishDate,
              amount: this.libs[index].amount,
              borrowstate: this.libs[index].borrowstate,
              classify: this.libs[index].classify,
            },
            data: {},
            //post
            //node
            headers: {'content-Type': 'application/x-www-form-urlencoded'},
          }).then(function (response) {
            console.log(response)
          }).catch(function (error) {
            console.log(error);
          });
        } else {    //
          axios({
            method: 'post',
            url: 'http://localhost:3000/magazineManage/insert',
            params: {
              mid: this.libs[index].mid,
              name: this.libs[index].name,
              publishDate: this.libs[index].publishDate,
              amount: this.libs[index].amount,
              borrowstate: this.libs[index].borrowstate,
              classify: this.libs[index].classify,
            },
            data: {},
            //post
            //node
            headers: {'content-Type': 'application/x-www-form-urlencoded'},
          }).then(function (response) {
            console.log(response.data);
            if (response.data === "success!") {
              that.$message({
                message: '',
                type: 'success'
              });
            }
          }).catch(function (error) {
            console.log(error);
          });
        }*/
        this.$forceUpdate();
      }
    },
    // 
    deleting(index) {
      //
     /* let that=this;
      let deletedMid = this.libs[index].mid;
      axios({
        method: 'post',
        url: 'http://localhost:3000/magazineManage/delete',
        params: {
          mid: this.libs[index].mid,
        },
        data: {},
        //post
        //node
        headers: {'content-Type': 'application/x-www-form-urlencoded'},
      }).then(function (response) {
        console.log(response.data);
        if (response.data === "success!") {
          that.$message({
            message: "" + deletedMid + "",
            type: 'success'
          });
        }
      }).catch(function (error) {
        console.log(error);
      });*/

      //index0
      if (index === 0) {//
        this.libs.shift();
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      } else if (index + 1 === this.libs.length) {//
        this.libs.pop();
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      } else {//
        this.libs.splice(index, 1);
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      }
      if (this.libs.length === 0) {//
        this.lastpage();
      }
    },
    // 
    lastpage() {
      if (this.nowPage > 1) {//
        for (let i = 0; i < 8; i++) {
          this.libs[i] = this.libsDatabase[(this.nowPage - 2) * 8 + i];
        }
        this.nowPage--;
        this.$forceUpdate();//
      }
    },
    // 
    nextpage() {
      if (this.nowPage < this.totalPage) {//
        this.libs = [];//
        if ((this.totalPage - this.nowPage) > 1) { //8
          for (let i = 0; i < 8; i++) {
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];//
          }
        } else if (this.totalPage - this.nowPage === 1) {//8
          for (let i = 0; i < this.libsDatabase.length % 8; i++) {
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];
          }
        }
        this.nowPage++;
        this.$forceUpdate();
      }
    },
    //
    homepage() {
      if (this.nowPage > 1) {
        for (let i = 0; i < 8; i++) {
          this.libs[i] = this.libsDatabase[i];
        }
        this.nowPage = 1;
        this.$forceUpdate();
      }
    },
    // 
    trailerpage() {
      if (this.nowPage < this.totalPage) {
        this.libs = [];
        if ((this.libsDatabase.length % 8) !== 0) {//8
          for (let i = 0; i < this.libsDatabase.length % 8; i++) {
            this.libs[i] = this.libsDatabase[(this.totalPage - 1) * 8 + i];
          }
        } else {
          for (let i = 0; i < 8; i++) {//8
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];
          }
        }
        this.nowPage = this.totalPage;
        this.$forceUpdate();
      }
    },
    initalize(){//
      this.libs=[];
      if (this.libsDatabase.length >= 8) {
        for (let i = 0; i < 8; i++) {   //
          this.libs.push(this.libsDatabase[i])
        }//libdatabase
        this.totalPage = parseInt(this.libsDatabase.length / 8) + 1;
        this.nowPage = 1;
      }else{
        for (let i = 0; i < this.libsDatabase.length; i++) {   //
          this.libs.push(this.libsDatabase[i])
        }
      }
    },
    // -------------------------------------------------------
    linkDatabase() {
      this.libsDatabase=[
        {
          mid: "M0001",
          name: "",
          publishDate: "20121225",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          mid: "M0002",
          name: "",
          publishDate: "202097",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          mid: "M0003",
          name: "",
          publishDate: "201861",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          mid: "M0004",
          name: "",
          publishDate: "202071",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          mid: "M0005",
          name: "VOGUE",
          publishDate: "202095",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          mid: "M0006",
          name: "",
          publishDate: "202111",
          amount: 1,
          borrowstate: "",
          classify: "",
        },
      ]
/*      this.libsDatabase=[];
      axios.get('http://localhost:3000/magazineManage/get').then((response) => {
        let that = this.libsDatabase;
        response.data.forEach(function (value, index) {
          that.push(response.data[index]);
        })
      })*/
      this.initalize();
    }
    // -------------------------------------------------------
  },
  created() {
    this.linkDatabase();//
  }
}
</script>

<style>
@import '/src/assets/css/BookManage.css';
</style>