<template>
  <div id="BookManage">
    <div id="head">
      <ul>
        <li>
          <el-button type="primary" @click="adddata"><i class="el-icon-circle-plus"/></el-button>
        </li>
        <li></li>
        <li>
          <el-select v-model="value" placeholder="">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
        </li>
        <li>
          <span>  </span>
        </li>
        <li @keyup.enter="searchBook">
          <el-input placeholder="" v-model:value="searchValue"></el-input>
        </li>
        <li>
          <el-button type="primary" @click="searchBook"></el-button>
        </li>
      </ul>
    </div>
    <div id="main">
      <el-empty description="" v-show="false"></el-empty>
      <div id="sort">
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
      <div id="detail">
        <ul v-for="(item,index) in libs">
          <li><input :disabled="true" v-model:onfocusout="libs[index].uid"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].borrowBook"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].borrowDate"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].borrowAmount"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].borrowdebt"></li>
          <li>
            <el-button id="amend" type="primary" v-show="editAble[index]" @click="amending(index)">
              
            </el-button>
            <el-button id="save" type="primary" v-show="showSaved[index]" @click="saving(index)"></el-button>
            <el-button id="delete" type="primary" @click="deleting(index)"></el-button>
          </li>
        </ul>
      </div>
    </div>
    <div id="bottom">
      <ul>
        <li>
          <el-button type="primary" @click="homepage"></el-button>
        </li>
        <li>
          <el-button @click="lastpage"></el-button>
        </li>
        <li>{{ nowPage }}/{{ totalPage }}</li>
        <li>
          <el-button @click="nextpage"></el-button>
        </li>
        <li>
          <el-button type="primary" @click="trailerpage"></el-button>
        </li>
        <li>{{ this.libs.length }}</li>
      </ul>
    </div>
  </div>
</template>

<!--vue-->
<script>
import axios from "axios";

export default {
  name: "BorrowManage",
  data() {
    return { //return
      libs: [],
      libsSearch: [],//
      searchValue: "",
      libsDatabase: [],//
      transitDatabase: [],//
      nowPage: 1,
      totalPage: 1,
      allowed: false,
      showSaved: [false, false, false, false, false, false, false, false],
      editAble: [true, true, true, true, true, true, true, true],
      saveMode: 'amend',
      options: [
        {
          value: 'uid',
          label: ''
        }, {
          value: 'borrowBook',
          label: ''
        }, {
          value: 'borrowDate',
          label: ''
        }, {
          value: 'borrowdebt',
          label: ''
        }, {
          value: 'borrowAmount',
          label: ''
        },],
      value: 'borrowBook'
    }
  },
  methods: {

    searchBook() {
      if (this.searchValue !== "") {
        this.libsSearch = []; //
        for (let i = 0; i < this.libsDatabase.length; i++) {
          switch (this.value) {
            case "uid":
              if (this.libsDatabase[i].uid === this.searchValue) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "borrowBook":  //
              if (this.strMatch(this.libsDatabase[i].borrowBook, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "borrowDate"://
              if (this.strMatch(this.libsDatabase[i].borrowDate, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "borrowAmount":
              if (this.libsDatabase[i].borrowAmount === parseInt(this.searchValue)) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "borrowdebt":
              if (this.libsDatabase[i].borrowdebt === parseInt(this.searchValue)) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
          }
        }
      }
      this.showData();
    },

    strMatch(str, key) {// 
      let newkey = key.split(/\s/g);//
      for (let i = 0; i < newkey.length; i++) {
        if (str.match(newkey[i]) != null) {
          return true //matchtrue
        }
      }
      return false
    },

    showData() {
      if (this.searchValue !== "") {
        this.libsDatabase = this.libsSearch;
        this.initalize();
      } else {
        this.linkDatabase();
      }
    },

    adddata() {//
      if (this.libs.length < 8) { //
        if (this.editAble[this.libs.length - 1] !== false) {
          //Uxxxxuid
          let uidNum = parseInt(this.libs[this.libs.length - 1].uid.slice(-4)) + 1; //uid0
          let uidNumLen = uidNum.toString().length;//uid
          let uidNew;//uid
          switch (uidNumLen) {
            case 1:
              uidNew = "U000" + uidNum.toString();
              break;
            case 2:
              uidNew = "U00" + uidNum.toString();
              break;
            case 3:
              uidNew = "U0" + uidNum.toString();
              break;
            case 4:
              uidNew = "U" + uidNum.toString();
              break;
          }
        this.libs.push({
          uid: uidNew,
          borrowBook: "",
          borrowDate: "",
          borrowAmount: 1,
          borrowdebt: 0,
        });
        this.libsDatabase.push(this.libs[this.libs.length - 1]);
        this.saveMode = "insert";
        this.editAble[this.libs.length - 1] = false;
        this.showSaved[this.libs.length - 1] = true;
      }
      } else if (this.libs.length === 8) {//
        if ((this.totalPage - this.nowPage) >= 1) { //
          this.trailerpage();//
          this.adddata();//,
        } else if ((this.totalPage - this.nowPage) === 0) { //
          this.libs = [];//
          this.totalPage++;
          this.nowPage++;
          this.adddata();
        }
      }
    },
    // 
    amending(index) {
      this.editAble[index] = false;
      this.showSaved[index] = true;
      this.saveMode = "amend";
      this.$forceUpdate(); //
    },
    // 
    saving(index) {
      let that=this;
      if (this.libs[index].borrowBook === "" || this.libs[index].borrowDate === "") {
        this.$message({
          message: '',
          type: 'warning'
        });
      } else {
        this.editAble[index] = true;
        this.showSaved[index] = false;
/*        if (this.saveMode === "amend") {
          axios({    //
            method: 'post',
            url: 'http://localhost:3000/borrowManage/amend',
            params: {
              uid: this.libs[index].uid,
              borrowBook: this.libs[index].borrowBook,
              borrowDate: this.libs[index].borrowDate,
              borrowAmount: this.libs[index].borrowAmount,
              borrowdebt: this.libs[index].borrowdebt,
            },
            data: {},
            //post
            //node
            headers: {'content-Type': 'application/x-www-form-urlencoded'},
          }).then(function (response) {
            console.log(response)
          }).catch(function (error) {
            console.log(error);
          });
        } else {    //
          axios({
            method: 'post',
            url: 'http://localhost:3000/borrowManage/insert',
            params: {
              uid: this.libs[index].uid,
              borrowBook: this.libs[index].borrowBook,
              borrowDate: this.libs[index].borrowDate,
              borrowAmount: this.libs[index].borrowAmount,
              borrowdebt: this.libs[index].borrowdebt,
            },
            data: {},
            //post
            //node
            headers: {'content-Type': 'application/x-www-form-urlencoded'},
          }).then(function (response) {
            console.log(response.data);
            if (response.data === "success!") {
              that.$message({
                message: '',
                type: 'success'
              });
            }
          }).catch(function (error) {
            console.log(error);
          });
        }*/
        this.$forceUpdate();
      }
    },
    // 
    deleting(index) {
      //
     /* let that=this;
      let deletedUid = this.libs[index].uid;
      axios({
        method: 'post',
        url: 'http://localhost:3000/borrowManage/delete',
        params: {
          uid: this.libs[index].uid,
        },
        data: {},
        //post
        //node
        headers: {'content-Type': 'application/x-www-form-urlencoded'},
      }).then(function (response) {
        console.log(response.data);
        if (response.data === "success!") {
          that.$message({
            message: "" + deletedUid + "",
            type: 'success'
          });
        }
      }).catch(function (error) {
        console.log(error);
      });
*/
      //index0
      if (index === 0) {//
        this.libs.shift();
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      } else if (index + 1 === this.libs.length) {//
        this.libs.pop();
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      } else {//
        this.libs.splice(index, 1);
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      }
      if (this.libs.length === 0) {//
        this.lastpage();
      }
    },
    // 
    lastpage() {
      if (this.nowPage > 1) {//
        for (let i = 0; i < 8; i++) {
          this.libs[i] = this.libsDatabase[(this.nowPage - 2) * 8 + i];
        }
        this.nowPage--;
        this.$forceUpdate();//
      }
    },
    // 
    nextpage() {
      if (this.nowPage < this.totalPage) {//
        this.libs = [];//
        if ((this.totalPage - this.nowPage) > 1) { //8
          for (let i = 0; i < 8; i++) {
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];//
          }
        } else if (this.totalPage - this.nowPage === 1) {//8
          for (let i = 0; i < this.libsDatabase.length % 8; i++) {
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];
          }
        }
        this.nowPage++;
        this.$forceUpdate();
      }
    },
    //
    homepage() {
      if (this.nowPage > 1) {
        for (let i = 0; i < 8; i++) {
          this.libs[i] = this.libsDatabase[i];
        }
        this.nowPage = 1;
        this.$forceUpdate();
      }
    },
    // 
    trailerpage() {
      if (this.nowPage < this.totalPage) {
        this.libs = [];
        if ((this.libsDatabase.length % 8) !== 0) {//8
          for (let i = 0; i < this.libsDatabase.length % 8; i++) {
            this.libs[i] = this.libsDatabase[(this.totalPage - 1) * 8 + i];
          }
        } else {
          for (let i = 0; i < 8; i++) {//8
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];
          }
        }
        this.nowPage = this.totalPage;
        this.$forceUpdate();
      }
    },
    initalize() {//
      this.libs=[];
      if (this.libsDatabase.length >= 8) {
        for (let i = 0; i < 8; i++) {   //
          this.libs.push(this.libsDatabase[i])
        }//libdatabase
        this.totalPage = parseInt(this.libsDatabase.length / 8) + 1;
        this.nowPage = 1;
      } else {
        for (let i = 0; i < this.libsDatabase.length; i++) {   //
          this.libs.push(this.libsDatabase[i])
        }
      }
    },
    // -------------------------------------------------------
    linkDatabase() {
        this.libsDatabase = [
          {
            uid: "U0001",
            borrowBook: "A",
            borrowDate: "202186",
            borrowAmount: 1,
            borrowdebt: 0,
          },          {
            uid: "U0002",
            borrowBook: "A",
            borrowDate: "2021720",
            borrowAmount: 3,
            borrowdebt: 0,
          },          {
            uid: "U0003",
            borrowBook: "SQL",
            borrowDate: "2021628",
            borrowAmount: 1,
            borrowdebt: 0,
          },          {
            uid: "U0004",
            borrowBook: "",
            borrowDate: "2021730",
            borrowAmount: 2,
            borrowdebt: 0,
          },          {
            uid: "U0005",
            borrowBook: "",
            borrowDate: "2021811",
            borrowAmount: 2,
            borrowdebt: 0,
          }
        ];
/*      axios.get('http://localhost:3000/borrowManage/get').then((response) => {
        let that = this.libsDatabase;
        response.data.forEach(function (value, index) {
          that.push(response.data[index]);
        })
      })*/
      this.initalize();
    }
    // -------------------------------------------------------
  },
  created() {
    this.linkDatabase();//
  }
}
</script>

<style scoped>
#BookManage {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
}

#head {
  width: 100%;
  height: 10%;
  background-color: #f0f0f0;
}

#head ul {
  list-style: none;
  height: 100%;
  margin: 0;
  transform: scale(0.92);
}

#head ul li {
  float: left;
  margin-left: 5px;
  height: 100%;
  display: flex;
  align-items: center;
  font-size: 15px;
}

#head ul li:nth-child(2) {
  margin-left: 5%;
}

#head ul li:nth-child(3) {
  width: 120px;
}

#main {
  width: 100%;
  height: 80%;
}

/**/
#sort {
  margin-top: 1%;
  width: 100%;
  height: 30px;
  line-height: 30px;
}

#sort ul {
  width: auto;
  height: 100%;
  list-style: none;
  margin: 0 auto;
}

#sort ul li {
  width: 11%;
  height: 100%;
  float: left;
  padding-left: 10px;
  color: #4e4e4e;
  font-size: 13px;
  border: 1px solid #d8d8d8;
  background-color: #f0f0f0;
}
#sort ul li:nth-child(2){
  width: 28%;
}
#sort ul li:last-child {
  width: 17%;
}

/* */
#detail {
  width: 100%;
  height: 50px;
  line-height: 50px;
}

#detail ul {
  width: auto;
  height: 100%;
  list-style: none;
  margin: 0 auto;
}

#detail ul li {
  width: 11%;
  height: 100%;
  float: left;
  padding-left: 10px;
  color: #4e4e4e;
  font-size: 13px;
  border: 1px solid #d8d8d8;
}
#detail ul li:nth-child(2){
  width: 28%;
}
#detail ul li input {
  width: 100%;
  height: 95%;
  padding: 0px;
  font-size: 13px;
  border: none;
}

input[disabled] {
  background: #ffffff;
  color: black;
}

#detail ul li:last-child {
  text-align: center;
  width: 17%;
}

#amend {
  border: none;
}

#save {
  background-color: #57c746;
  border: none;
  margin-left: -1px;
}

#save:hover {
  background-color: #79e071;
}

#delete {
  border: none;
  background-color: #ff4040;
}

#delete:hover {
  background-color: #ff7979;
}

/**/
#bottom {
  margin-top: 1%;
  width: 100%;
  height: 30px;
  line-height: 30px;
  transform: scale(.95);
}

#bottom ul {
  width: max-content;
  height: 100%;
  list-style: none;
  margin: 0 auto;
  padding: 0;
}

#bottom ul li {
  width: 100px;
  height: 100%;
  float: left;
  color: #4e4e4e;
  text-align: center;
  font-size: 13px;
}

#bottom ul li:last-child {
  width: 120px;
}
</style>