<template>
  <div id="system">
    <!--    -->
    <div id="manual">
      <Manual></Manual>
    </div>
    <div id="displayArea">
      <!--      -->
      <NavigationBar></NavigationBar>
      <!--      -->
      <div id="displayArea_main">
<!--        :include="setAlive"namesetAlive router-metakeepalive-->
        <keep-alive :include="setAlive">
        <router-view  :key="key"></router-view>
        </keep-alive>
<!--        <router-view v-if="!$route.meta.keepAlive" :key="key"></router-view>-->
      </div>
    </div>
  </div>
</template>

<script>
import Manual from "../components/Manual";
import NavigationBar from "../components/NavigationBar";

export default {
  name: "System",
  components: {
    Manual,
    NavigationBar
  },
  data(){
    return{
      setAlive:this.$store.state.alivelist
    }
  },
  computed:{
    key(){
      return this.$route.path
    }
  },
  methods:{
    setAliveFun(){
      this.setAlive=this.$store.state.alivelist;
    }
  },
  watch: {
    $route(to, from) {
      this.setAliveFun();
      this.$store.commit('change');
    }
  }
}
</script>

<style scoped>
#system {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
}
#manual {
  min-width: 155px;
  height: 100%;
  color: white;
  background-color: #438aff;
}

#displayArea {
  flex-grow: 1;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
}

#displayArea_main {
  width: 100%;
  height: 100%;

}
</style>