<template>
  <div id="BookManage">
    <div id="head">
      <ul>
        <li>
          <el-button type="primary" @click="adddata"><i class="el-icon-circle-plus"/></el-button>
        </li>
        <li></li>
        <li>
          <el-select v-model="value" placeholder="">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
        </li>
        <li>
          <span>  </span>
        </li>
        <li @keyup.enter="searchBook">
          <el-input placeholder="" v-model:value="searchValue" @keyup.enter="searchBook"></el-input>
        </li>
        <li>
          <el-button type="primary" @click="searchBook"></el-button>
        </li>
      </ul>
    </div>
    <div id="main">
      <el-empty description="" v-show="false"></el-empty>
      <div id="sort">
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
      <div id="detail">
        <ul v-for="(item,index) in libs">
          <li><input :disabled="true" v-model:onfocusout="libs[index].bid"></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].name" placeholder=""></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].publishhouse" placeholder=""></li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].amount"
                     onkeyup="if (this.value.match(/\D+/g)||this.value.match(/\d{6,}/g) !==null){this.value=this.value.replace(/.*/,'0');alert('5')}">
          </li>
          <!--                     -->
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].borrowstate"
                     onblur="if (this.value !=='' && this.value!==''){this.value=this.value.replace(/.*/,'');alert('');}">
          </li>
          <li><input :disabled="editAble[index]" v-model:onfocusout="libs[index].classify" placeholder=""></li>
          <li>
            <el-button id="amend" type="primary" v-show="editAble[index]" @click="amending(index)">
              
            </el-button>
            <el-button id="save" type="primary" v-show="showSaved[index]" @click="saving(index)"></el-button>
            <el-button id="delete" type="primary" @click="deleting(index)"></el-button>
          </li>
        </ul>
      </div>
    </div>
    <div id="bottom">
      <ul>
        <li>
          <el-button type="primary" @click="homepage"></el-button>
        </li>
        <li>
          <el-button @click="lastpage"></el-button>
        </li>
        <li>{{ nowPage }}/{{ totalPage }}</li>
        <li>
          <el-button @click="nextpage"></el-button>
        </li>
        <li>
          <el-button type="primary" @click="trailerpage"></el-button>
        </li>
        <li>{{ this.libs.length }}</li>
      </ul>
    </div>
  </div>
</template>

<!--vue-->
<script>
import axios from "axios";

export default {
  name: "BookManage",
  data() {
    return { //return
      libs: [],//
      libsSearch: [],//
      searchValue: "",//
      libsDatabase: [],//
      nowPage: 1,//
      totalPage: 1,//
      showSaved: [false, false, false, false, false, false, false, false],
      editAble: [true, true, true, true, true, true, true, true],
      saveMode: 'amend',
      options: [
        {
          value: 'bid',
          label: ''
        },
        {
          value: 'name',
          label: ''
        }, {
          value: 'publishhouse',
          label: ''
        }, {
          value: 'amount',
          label: ''
        }, {
          value: 'borrowstate',
          label: ''
        }, {
          value: 'classify',
          label: ''
        }],
      value: 'name'
    }
  },
  methods: {

    searchBook() {
      if (this.searchValue !== "") {
        this.libsSearch = []; //
        for (let i = 0; i < this.libsDatabase.length; i++) {
          switch (this.value) {
            case "bid"://bid
              if (this.libsDatabase[i].bid === this.searchValue) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "name":  //
              if (this.strMatch(this.libsDatabase[i].name, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "publishhouse"://
              if (this.strMatch(this.libsDatabase[i].publishhouse, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "amount":
              if (this.libsDatabase[i].amount === parseInt(this.searchValue)) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "borrowstate":
              if (this.libsDatabase[i].borrowstate === this.searchValue) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
            case "classify"://
              if (this.strMatch(this.libsDatabase[i].classify, this.searchValue) === true) {
                this.libsSearch.push(this.libsDatabase[i]);
              }
              break;
          }
        }
      }
      this.showData();
    },

    strMatch(str, key) {//
      let newkey = key.split(/\s/g);//
      for (let i = 0; i < newkey.length; i++) {
        if (str.match(newkey[i]) != null) {
          return true //matchtrue
        }
      }
      return false
    },

    showData() {
      if (this.searchValue !== "") {
        this.libsDatabase = this.libsSearch;
        this.initalize();
      } else {
        this.linkDatabase();
      }
    },

    adddata() {//
      if (this.libs.length < 8) { //
        if (this.editAble[this.libs.length - 1] !== false) {
          //Bxxxxbid
          let bidNum = parseInt(this.libs[this.libs.length - 1].bid.slice(-4)) + 1; //bid0
          let bidNumLen = bidNum.toString().length;//bid
          let bidNew;//bid
          switch (bidNumLen) {
            case 1:
              bidNew = "B000" + bidNum.toString();
              break;
            case 2:
              bidNew = "B00" + bidNum.toString();
              break;
            case 3:
              bidNew = "B0" + bidNum.toString();
              break;
            case 4:
              bidNew = "B" + bidNum.toString();
              break;
          }
          this.libs.push({
            bid: bidNew,
            name: "",
            publishhouse: "",
            amount: 1,
            borrowstate: "",
            classify: "",
          });
          this.libsDatabase.push(this.libs[this.libs.length - 1]);
          this.saveMode = "insert";
          this.editAble[this.libs.length - 1] = false;
          this.showSaved[this.libs.length - 1] = true;
        }
      } else if (this.libs.length === 8) {//
        if ((this.totalPage - this.nowPage) >= 1) { //
          this.trailerpage();//
          this.adddata();//,
        } else if ((this.totalPage - this.nowPage) === 0) { //
          this.libs = [];//
          this.totalPage++;
          this.nowPage++;
          this.adddata();
        }
      }
    },
    // 
    amending(index) {
      this.editAble[index] = false;
      this.showSaved[index] = true;
      this.saveMode = "amend";
      this.$forceUpdate(); //
    },
    // 
    saving(index) {
      let that=this;
      if (this.libs[index].name === "" || this.libs[index].publishhouse === "" || this.libs[index].classify === "") { //
        this.$message({
          message: '',
          type: 'warning'
        });
      } else {
        this.editAble[index] = true;
        this.showSaved[index] = false;
        /*if (this.saveMode === "amend") {
          axios({    //
            method: 'post',
            url: 'http://localhost:3000/bookManage/amend',
            params: {
              bid: this.libs[index].bid,
              name: this.libs[index].name,
              publishhouse: this.libs[index].publishhouse,
              amount: this.libs[index].amount,
              borrowstate: this.libs[index].borrowstate,
              classify: this.libs[index].classify,
            },
            data: {},
            //post
            //node
            headers: {'content-Type': 'application/x-www-form-urlencoded'},
          }).then(function (response) {
            console.log(response)
          }).catch(function (error) {
            console.log(error);
          });
        } else {    //
          axios({
            method: 'post',
            url: 'http://localhost:3000/bookManage/insert',
            params: {
              bid: this.libs[index].bid,
              name: this.libs[index].name,
              publishhouse: this.libs[index].publishhouse,
              amount: this.libs[index].amount,
              borrowstate: this.libs[index].borrowstate,
              classify: this.libs[index].classify,
            },
            data: {},
            //post
            //node
            headers: {'content-Type': 'application/x-www-form-urlencoded'},
          }).then(function (response) {
            console.log(response.data);
            if (response.data === "success!") {
              that.$message({
                message: '',
                type: 'success'
              });
            }
          }).catch(function (error) {
            console.log(error);
          });
        }*/
        this.$forceUpdate();//
      }
    },
    // 
    deleting(index) {
      //
/*      let that=this;
      let deletedBid = this.libs[index].bid;
      axios({
        method: 'post',
        url: 'http://localhost:3000/bookManage/delete',
        params: {
          bid: this.libs[index].bid,
        },
        data: {},
        //post
        //node
        headers: {'content-Type': 'application/x-www-form-urlencoded'},
      }).then(function (response) {
        console.log(response.data);
        if (response.data === "success!") {
          that.$message({
            message: "" + deletedBid + "",
            type: 'success'
          });
        }
      }).catch(function (error) {
        console.log(error);
      });*/

      //index0
      if (index === 0) {//
        this.libs.shift();
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      } else if (index + 1 === this.libs.length) {//
        this.libs.pop();
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      } else {//
        this.libs.splice(index, 1);
        this.libsDatabase.splice((this.nowPage - 1) * 8 + index, 1);
      }
      if (this.libs.length === 0) {//
        this.lastpage();
      }
    },
    // 
    lastpage() {
      if (this.nowPage > 1) {//
        for (let i = 0; i < 8; i++) {
          this.libs[i] = this.libsDatabase[(this.nowPage - 2) * 8 + i];
        }
        this.nowPage--;
        this.$forceUpdate();//
      }
    },
    // 
    nextpage() {
      if (this.nowPage < this.totalPage) {//
        this.libs = [];//
        if ((this.totalPage - this.nowPage) > 1) { //8
          for (let i = 0; i < 8; i++) {
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];//
          }
        } else if (this.totalPage - this.nowPage === 1) {//8
          for (let i = 0; i < this.libsDatabase.length % 8; i++) {
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];
          }
        }
        this.nowPage++;
        this.$forceUpdate();
      }
    },
    //
    homepage() {
      if (this.nowPage > 1) {
        for (let i = 0; i < 8; i++) {
          this.libs[i] = this.libsDatabase[i];
        }
        this.nowPage = 1;
        this.$forceUpdate();
      }
    },
    // 
    trailerpage() {
      if (this.nowPage < this.totalPage) {
        this.libs = [];
        if ((this.libsDatabase.length % 8) !== 0) {//8
          for (let i = 0; i < this.libsDatabase.length % 8; i++) {
            this.libs[i] = this.libsDatabase[(this.totalPage - 1) * 8 + i];
          }
        } else {
          for (let i = 0; i < 8; i++) {//8
            this.libs[i] = this.libsDatabase[this.nowPage * 8 + i];
          }
        }
        this.nowPage = this.totalPage;
        this.$forceUpdate();
      }
    },
    initalize() {//
      this.libs = [];
      if (this.libsDatabase.length >= 8) {
        for (let i = 0; i < 8; i++) {   //
          this.libs.push(this.libsDatabase[i])
        }//libdatabase
        this.totalPage = parseInt(this.libsDatabase.length / 8) + 1;
        this.nowPage = 1;
      } else {
        for (let i = 0; i < this.libsDatabase.length; i++) {   //
          this.libs.push(this.libsDatabase[i])
        }
      }
    },
    // -------------------------------------------------------
    linkDatabase() {
      this.libsDatabase = [
        {
          bid: "B0001",
          name: "SQL",
          publishhouse: "",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          bid: "B0002",
          name: "C",
          publishhouse: "",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          bid: "B0003",
          name: "",
          publishhouse: "",
          amount: 3,
          borrowstate: "",
          classify: "",
        },        {
          bid: "B0004",
          name: "",
          publishhouse: "",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          bid: "B0005",
          name: "C# HeadFirst",
          publishhouse: "",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          bid: "B0006",
          name: "JavaScript",
          publishhouse: "",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          bid: "B0007",
          name: "C",
          publishhouse: "",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          bid: "B0008",
          name: "",
          publishhouse: "",
          amount: 1,
          borrowstate: "",
          classify: "",
        },        {
          bid: "B0009",
          name: "",
          publishhouse: "",
          amount: 5,
          borrowstate: "",
          classify: "",
        },        {
          bid: "B0010",
          name: "TestName",
          publishhouse: "TestPublishHouse",
          amount: 1,
          borrowstate: "",
          classify: "TestClassify",
        },
      ];
      // axios.get('http://localhost:3000/bookManage/get').then((response) => {
      axios.get('http://localhost:3000/bookManage/get').then((response) => {
        /*let data=JSON.stringify(response.data);*/
        //JSON.parse()  JSON 
        //JSON.stringify() JavaScript() JSON
        let that = this.libsDatabase;
        response.data.forEach(function (value, index) {
          that.push(response.data[index]);
        })
      })
      this.initalize();
    }
    // -------------------------------------------------------
  },
  created() {
    this.linkDatabase();//
  }
}
</script>

<style scoped>
@import '../../../assets/css/BookManage.css';
</style>