<template>
  <div id="manual">
  <el-row class="tac">
    <el-col :span="12" class="list">
      <el-menu
          :default-active=activeManual
          class="el-menu-vertical-demo"
          text-color="#fff">
        <!--      1-->
          <el-menu-item index="0" @click="mainPage();setActiveByNum(0)">
            <i class="el-icon-s-home"></i>
            <span slot="title" ref="Home" ></span>
          </el-menu-item>
        <!--      2-->
        <el-submenu index="A">
          <template slot="title">
            <i class="el-icon-s-help"></i>
            <span></span>
          </template>
          <el-menu-item-group>
            <router-link to="/system/BookManage">
              <el-menu-item index="1" @click="open(1);setActiveByNum(1)"><i class="el-icon-reading"></i></el-menu-item>
            </router-link>
            <router-link to="/system/MagazineManage">
              <el-menu-item index="2" @click="open(2);setActiveByNum(2)"><i class="el-icon-document"></i></el-menu-item>
            </router-link>
            <router-link to="/system/BorrowManage">
              <el-menu-item index="3" @click="open(3);setActiveByNum(3)"><i class="el-icon-notebook-1"></i></el-menu-item>
            </router-link>
            <router-link to="/system/StaffManage">
              <el-menu-item index="4" @click="open(4);setActiveByNum(4)"><i class="el-icon-user"/></el-menu-item>
            </router-link>
          </el-menu-item-group>
        </el-submenu>
        <!--      3-->
        <router-link to="/system/ProClaim">
          <el-menu-item index="5" @click="open(5);setActiveByNum(5)">
            <i class="el-icon-data-board"></i>
            <span slot="title" ></span>
          </el-menu-item>
        </router-link>
        <!--      4-->
        <router-link to="/system/TestPage" >
        <el-menu-item index="6" @click="open(6);setActiveByNum(6)">
          <i class="el-icon-video-camera-solid"></i>
          <span slot="title"></span>
        </el-menu-item>
        </router-link>
      </el-menu>
    </el-col>
  </el-row>
  </div>
</template>

<script>
import {mapMutations, mapState} from "vuex";

export default {
  name: "Manual",
  data() {
    return {
      activeManual: "0"
    }
  },
  methods: {//
    ...mapMutations(['open', 'setActiveByNum']),//store index.jsopen
    mainPage() {
      this.$router.push({path: '/system/DefaultManual'});
      this.open(0);
    },
  },
  computed: {
    ...mapState(['activeNum']),
    resetMainPage() {
      return this.$store.state.taglength
    },
    activeManualNum() {
      return this.activeNum
    }
  },
  watch: {
    resetMainPage(newdata, olddata) {
      if (newdata === 0) {
        this.mainPage();
      }
    },
    activeManualNum(newdata, olddata) {
      this.activeManual = newdata.toString();
      // }
    }
    /*  ref="defaultclick"   //
    mounted() {
      this.$refs.defaultclick.click();//
    }*/
    /*   watch: { //
    $route(to, from) {
      let name = to.path.match(/[^/system/].+/g);
      if (name.toString() === "DefaultManual") {
        this.$refs.Home.click();
      }
    }
  } */
  }
}
</script>

<style scoped>
a { /*router-linka*/
  text-decoration: none;
}

.list {
  width: 100%;
}

>>> .el-menu-item.is-active {
  color: #ffc400;
  background-color: #2e60b1;
}

>>> .el-submenu__title i { /* */
  color: white;
}

>>> .el-submenu__title { /**/
  background-color: #438aff;
}

>>> .el-submenu .el-menu { /* */
  background-color: #438aff;
}

.el-menu-item { /**/
  min-width: 130px !important;
}

/* hover*/
>>> .el-submenu__title:hover { /*hover*/
  background-color: #2e60b1;
}

.el-menu-item:hover { /*hover*/
  background-color: #2e60b1;
}

.el-menu-item i { /* */
  color: white;
}

.el-menu { /**/
  border: none;
  background-color: #438aff;
}
</style>